<template>
  <div class="Initialization">
    <div class="file_box">
      <img class="link_File" src="@/assets/Link_File.png" alt="Link_File" :style="linkFileStyle" />
    </div>

    <p class="title_info">汉化位置</p>
    <p class="title_info_path" v-if="GamesInstallPathSave">{{GamesInstallPath}}</p>

    <p class="info_details">
      <span style="text-indent: 10px;" v-if="!GamesInstallPathSave">
        请把桌面快捷方式《Игровой центр》拖动到这里或者进行
        <span
          style="color:#228be6;font-weight: bold;cursor: pointer;"
        >手动设定</span>。
      </span>
      <!-- <span v-else style="">
        游戏安装位置
        <span
          style="color:#228be6;font-weight: bold;"
        >{{GamesInstallPath}}</span>
      </span>-->
    </p>
    <!-- <p class="info_details" style="text-align: center;text-indent: 0px;">
     
      <span>
        正在校验文件请稍后
      </span>
    </p>-->

    <div class="btn_group">
      <button class="lostark_btn manual" style="position: relative;" v-if="!GamesInstallPathSave">
        <i class="lostark iconLink" style="font-size:14px;vertical-align: middle;"></i>
        手动设定
        <input type="file" @change="changeFile" name="" style="opacity: 0;
    position: absolute;
    cursor: pointer;
    left: 0;
    width: 108px;
    height: 33px;">
      </button>

      <!-- <button class="lostark_btn delete"  v-if="GamesInstallPathSave ==true && isReduction == true" @click="deleteFile">删 除</button> -->
      <button
        class="lostark_btn save"
        v-if="GamesInstallPathSave ==true && isReduction == true"
        @click="cover"
      >确 定</button>

      <button
        class="lostark_btn reduction"
        v-if="GamesInstallPathSave == true && isReduction == false"
        @click="reduction"
      >还 原</button>

      
    </div>
  </div>
</template>

<script>
export default {
  name: "Initialization",

  data() {
    return {
      GamesInstallPathSave: this.$GamesInstallPath.isSave,
      GamesInstallPath: this.$GamesInstallPath.path,
      fileName: "Игровой центр",
      linkFileStyle: "",
      reader: {},
      typeMode: 1, //拖拽方式1 2手动,
      isReduction:true,//是否还原
    };
  },
  mounted() {
    let upload = document.querySelector(".Initialization");
    upload.addEventListener("dragenter", this.onDrag, false);
    upload.addEventListener("dragover", this.onDrag, false);
    upload.addEventListener("drop", this.onDrop, false);
    this.reader = new FileReader();
    if (this.GamesInstallPathSave == true) {
      this.linkFileStyle = "opacity: 1;";
      //直接覆盖
     
    }else{
       setTimeout(()=>{
     this.$electron.ipcRenderer.send("get-filer-message");
      console.log(123)
     },1000)
        this.$electron.ipcRenderer.on("filer-message-reply", (event, path) => {
          console.log("path", path); // 印出 "pong"
          this.$GamesInstallPath.path = path;
          this.$GamesInstallPath.isSave = true;
          this.GamesInstallPathSave = true;
          this.GamesInstallPath = path;
          this.linkFileStyle = "opacity: 1;";
        });
    }

    this.$electron.ipcRenderer.on("error-reply", (event, msg) => {
      alert(msg)
    })
     
  },

  methods: {
    changeFile(e){
      if(e.target.files[0].name == 'font.lpk'){
        this.$GamesInstallPath.path = e.target.files[0].path;
          this.$GamesInstallPath.isSave = true;
          this.GamesInstallPath = e.target.files[0].path;
          this.GamesInstallPathSave = true
          this.linkFileStyle = "opacity: 1;";
      }else{
        alert('请选择正确font.lpk')
      }
    },
    bufFunction(buf) {
      console.log("bufFunction");
      return String.fromCharCode.apply(null, new Uint8Array(buf));
    },
    deleteFile() {
      if (this.GamesInstallPathSave) {
        this.$GamesInstallPath.path = "";
        this.$GamesInstallPath.isSave = false;
        this.linkFileStyle = "opacity: 0.1;";
        this.GamesInstallPathSave = false;
        this.fileName = "汉化设置";
         this.$electron.ipcRenderer.send("delete-file-message",this.$GamesInstallPath.isSave);
     
      }
    },
    onDrag(e) {
      e.stopPropagation();
      e.preventDefault();
      // console.log("1", e);
    },
    //还原操作
    reduction(){
      //拖拽方式1 手动2
      let path = "";
      if (this.typeMode == 1) {
        path = this.$GamesInstallPath.path;
      } else {
        path = this.$GamesInstallPath.path;
      }
      this.$electron.ipcRenderer.send("reduction-message", path);
      this.$electron.ipcRenderer.on("reduction-message-reply", (event, msg) => {
        console.log(msg);
        //成功还是失败
        this.isReduction = true
      });
    },
    //覆盖操作
    cover() {
      //拖拽方式1 手动2
      let path = "";
      if (this.typeMode == 1) {
        path = this.$GamesInstallPath.path;
      } else {
        path = this.$GamesInstallPath.path;
      }
      this.$electron.ipcRenderer.send("cover-message", path);
      this.$electron.ipcRenderer.on("cover-message-reply", (event, msg) => {
        console.log(msg);
        //成功还是失败
        this.isReduction = false
      });
    },
    //拖拽完成时
    onDrop(e) {
      
      // if (this.$GamesInstallPath.isSave != true) {
        
      //   if (!/\.(lnk)$/.test(e.dataTransfer.files[0].name)) {
      //     alert("请添加快捷方式");
      //     e.stopPropagation();
      //     e.preventDefault();
      //     return false;
      //   }
      //   let filesName = e.dataTransfer.files[0].name
      //   let self = this;
      //   this.reader.onload = ce => {
      //     console.log('xxx',ce.currentTarget.result)
      //     var matchReg = /(?<=BOOTCAMP).*?\.exe/gi;
      //     if (!ce.currentTarget.result.match(matchReg)) {
      //       alert("文件解析失败请放入正确文件");
      //     } else {
      //       self.linkFileStyle = "opacity: 1;";
      //       self.GamesInstallPathSave = true;
      //       let lnkStr = ce.currentTarget.result
            
      //         .match(matchReg)[0]
      //         .replace("\u0000", "")
      //         .replace(".exe", ".ini");
      //       self.$electron.ipcRenderer.send("file-message", lnkStr);
      //     }
          
      //   };
        
      //   this.$electron.ipcRenderer.on("file-message-reply", (event, path) => {
      //     console.log("path", path); // 印出 "pong"
      //     self.$GamesInstallPath.path = path;
      //     self.$GamesInstallPath.isSave = true;
      //     self.GamesInstallPath = path;
      //     self.fileName = filesName;
      //   });
      //   this.reader.readAsText(e.dataTransfer.files[0],'UTF-16LE');
      // }
      

      
      e.stopPropagation();
      e.preventDefault();
      // this.imgPreview(e.dataTransfer.files);
      // this.uploadFile(e.dataTransfer.files);
    }
  }
};
</script>

<style scoped lang="scss">
.Initialization {
  position: relative;
  width: 100%;
  text-align: center;
  .file_box {
    width: 100%;
    text-align: center;
    margin-top: 35px;

    .link_File {
      -webkit-user-drag: none;
      width: 100px;
      height: 100px;
      opacity: 0.1;
      display: inline-block;
      margin-bottom: 20px;
      transition: all 0.3s;
    }
  }

  .title_info {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
  }
  .title_info_path {
    text-align: left;
    display: inline-block;
    margin: 0 auto;
    width: 90%;
    font-size: 12px;
    color: #bbb;
    line-height: 20px;
    word-wrap: break-word;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3; //需要控制的文本行数
    overflow: hidden;
  }
  .info_details {
    text-indent: 10px;
    font-size: 12px;
    color: #f8f9fa;
    text-align: left;
    padding: 0 20px;
    line-height: 20px;
  }

  .btn_group {
    position: absolute;
    text-align: center;
    width: 100%;
    top: 250px;
    margin: 0 auto;
   
    .lostark_btn {
      cursor: pointer;
      display: inline-block;
      width: 100%;
      color: #fff;
      text-align: center;
      line-height: 35px;
      font-size: 12px;
      border-radius: 5px;

      border: none;
      transition: all 0.2s;
      max-width: 40%;
    }
    .delete {
      background: #ff922b;
      font-weight: 400;
    }
    .delete:hover {
      background: #d4751c;
    }
     .reduction{
      background: #20c997;
      font-weight: 400;
    }
    .reduction:hover {
      background: #16af81;
    }
    .manual {
      background: #228be6;
      font-weight: 400;
    }
    .manual:hover {
      background: rgba(32, 125, 207, 0.8);
    }
    .save:hover {
      color: #fff;
      background: #20c997;
    }
    .save {
      background: #fff;
      color: #000;
      font-weight: bold;
    }
  }
}
</style>
